<template>
	<div>
		<el-tabs :tab-position="tabPosition" @tab-click="handleClick"> <!--@tab-click='login'-->
    <el-tab-pane :label="tab0"></el-tab-pane>
    <el-tab-pane label="活动"></el-tab-pane>
    <el-tab-pane label="圈子"></el-tab-pane>
    <el-tab-pane label="好友"></el-tab-pane>
    <el-tab-pane label="订阅"></el-tab-pane>
  </el-tabs>
	</div>

</template>
<script>
  let thisa=null;
  export default {
    data() {
      thisa = this;
      return {
        tab0:'登陆',
        tabPosition: 'top'
      };
    },
    methods:{
      handleClick:function(tab,evt){
          thisa.$emit('clickTab',tab.label);
      },
    }
  };
</script>


<style type="text/css" media="screen">
</style>
